
body {
  margin: 0;
  text-align: center;
}

.navbar {
  display: flex;
  align-items: center;
  background: linear-gradient(90deg,
                                    rgba(255,255,255,1) 50%,
                                    rgba(198, 198, 207, 0.808)  70%,
                                    rgb(23, 23, 85)  80%,
                                    rgb(0, 14, 59) 100%);
  color: rgb(0, 14, 59);
  font-family: Helvetica;
  font-weight: 300;
}

.navbar__title {
  font-family: Lucy Thr Cat;
  margin-right: auto;
  font-size: 180%;
  padding: 12px 16px;
}

.navbar__item {
  padding: 1em;
  cursor: pointer;
  vertical-align: middle;
}
.navbar :hover{
background: white;
color: black;
}

@font-face {
  font-family: Lucy Thr Cat;
  src: url(fonts/lucythecatregular-bg9x-webfont.woff);
}

#tableContainer{
  margin: 3em;
}

.table{
  background: rgba(203, 255, 203, 0.568);
}

td{
  width: 15em;
}
th{
  font-family: 'Lobster', cursive;
  font-size: 14px;
  color:rgb(0, 14, 59);
  background:#7e7e9e52;
  height: 3em;
}

#analyticsBtn, #plusBtn{
  width: 2em;
}

.yesOrNo{
width: 1em;
}

.delete{
  width: 1.5em;
}

#plusContainer, #deleteContainer :hover {
  cursor: pointer;
}

input
{
    color: rgb(0, 14, 59);
    padding: 0;
    margin: 0;
    border: 0;
    background-color: transparent;
}
#cover
{
  margin-right: 23%;
  height: 2.2em;
  width: 20em;
  border-radius: 20px;
  background: linear-gradient(-90deg,
  #E5E5E9  10%,
  #EAEAED  25%,
  #F3F3F5 40%,
  rgb(23, 23, 85)  50%,
  rgb(0, 14, 59) 100%);
}

input[type="text"]
{
  height: 100%;
  width: 85%;
  color:  rgb(255, 255, 255);
  font-size: 20px;
    line-height: 1;
    font-family: 'Lobster', cursive;
    background: none;
}

input[type="text"]::placeholder
{
    color: rgb(255, 255, 255);
    font-family: 'Lobster', cursive;
  }

.icon{
  font-family: 'Lobster', cursive;
}